/* Functions */
@function rem-calc($size) {
  $remSize: $size / 16;
  @return #{$remSize}rem;
}

/* Mixins */
@mixin transition ($transition) {
  transition: $transition;
  -webkit-transition: $transition;
  -moz-transition: $transition;
  -ms-transition: $transition;
  -o-transition: $transition;
}

@mixin transition-delay($transition-delay){
  transition-delay: $transition-delay;
  -webkit-transition-delay: $transition-delay;
  -moz-transition-delay: $transition-delay;
  -ms-transition-delay: $transition-delay;
  -o-transition-delay: $transition-delay;
}

@mixin user-drag($user-drag){
  -webkit-user-drag: $user-drag;
  -khtml-user-drag: $user-drag;
  -moz-user-drag: $user-drag;
  -o-user-drag: $user-drag;
  user-drag: $user-drag;
}

@mixin user-select($user-select){
  -webkit-user-select: $user-select;
  -moz-user-select: $user-select;
  -ms-user-select: $user-select;
  -o-user-select: $user-select;
  user-select: $user-select;
}

@mixin border-radius($border-radius){
  border-radius: $border-radius;
  -webkit-border-radius: $border-radius;
  -moz-border-radius: $border-radius;
  -ms-border-radius: $border-radius;
  -o-border-radius: $border-radius;
}

@mixin background-size($background-size){
  background-size: $background-size;
  -webkit-background-size: $background-size;
  -moz-background-size: $background-size;
  -ms-background-size: $background-size;
  -o-background-size: $background-size;
}

@mixin transform-scale($transform-scale){
  transform: scale($transform-scale);
  -webkit-transform: scale($transform-scale);
  -moz-transform: scale($transform-scale);
  -ms-transform: scale($transform-scale);
  -o-transform: scale($transform-scale);
}

@mixin transform($transform){
  transform: $transform;
  -webkit-transform: $transform;
  -moz-transform: $transform;
  -ms-transform: $transform;
  -o-transform: $transform;
}

@mixin box-shadow($box-shadow){
  box-shadow: $box-shadow;
  -webkit-box-shadow: $box-shadow;
  -moz-box-shadow: $box-shadow;
  -ms-box-shadow: $box-shadow;
  -o-box-shadow: $box-shadow;
}
