/* Colors */
$white: #fff;
$black: #000;
$grey:#969696;
$med-grey:#4A4A4A;
$dark-grey:#555555;
$intel-blue: #0071c5;
$intel-sky-blue: #00aeef;
$intel-sky-blue-hover:#0083d7;
$intel-dark-blue: #003c71;
$intel-yellow: #f3d54e;
$intel-yellow-hover:#f1c425;
$border-color:#F0F0F0;
$intel-gradient: linear-gradient(180deg,#003c71,#0071c5);
/* Dimensions & Padding */
$app-width:rem-calc(1200);
$app-height:rem-calc(600);
$app-padding:rem-calc(85);
$app-padding-sm:.9375rem;
/* Breakpoints */
$breakpoint1: "only screen and (max-width:968px)";
$breakpoint2:"only screen and (max-width:692px)";
$breakpoint3: "only screen and (max-width:375px)";
