html.is-reveal-open{
    top: 0 !important;
}
html.is-reveal-open-in-iframe{
    position: relative !important;
}
.reveal.full{
    background-color: $white;
    color:$dark-grey;
    border:0;
    width:100%;
    max-width: $app-width;
    height: 100%;
    max-height: 602px !important;
    min-height:auto;
    padding:0;
    margin:0 auto !important;
    position: fixed;
    z-index: 1;
    border:1px solid #979797;
    @media #{$breakpoint1}{
        max-height:602px !important;
        overflow:auto;
        height: 100%;
    }
    &.mui-enter{
        position: absolute !important; // position absolute on slide in
        border:0;
        @media #{$breakpoint1}{
            position: fixed !important; // position fixed on slide in
        }
    }
    nav{
        position: absolute;
        z-index: 999;
        @media #{$breakpoint1}{
            position: fixed;
            top: 1px;
            .legal-button{
                background-color: transparent;
                background-image: url("../images/icons/icon-legal-alt.svg");
            }
            &:before{
                z-index:0;
                content:'';
                position: absolute;
                left:0;
                top: 0;
                height:50px;
                width: 100%;
                background: linear-gradient(0deg,rgba(#000, 0),rgba(#000, .75));
            }
        }
    }
    .reveal-container{
        width: 100%;
        height: 100%;
        min-height: $app-height;
        .images-container{
            padding:0;
            @include background-size(cover);
            background-position: center center;
            background-repeat: no-repeat;
            display: flex;
            align-items: center;
            justify-content: center;
            z-index: 2;
            box-shadow:7px 0px 10px 0px rgba(0,0,0,.30);
            @media #{$breakpoint1}{
                width:100%;
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                padding:rem-calc(42) 0 rem-calc(42) 0; // nav height + half of nav height
                box-shadow:0px 3.5px 10px 0px rgba(0,0,0,.15);
            }
            @media #{$breakpoint2}{
                background-position: center top;
            }
            img{
                width: 100%;
                max-width:rem-calc(500);
                @media #{$breakpoint1}{
                    max-width:rem-calc(240);
                }
            }
            div{
                padding:rem-calc(50);
                @media #{$breakpoint1}{
                    padding:0;
                }
            }
        }
        .content-container{
            padding:0;
            background-color:$white;
            color:$dark-grey;
            display: flex;
            align-items: center;
            flex-direction: column;
            @media #{$breakpoint1}{
                width:100%;
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                background-color:transparent;
            }
            h1{
                font-size:rem-calc(39);
                line-height: rem-calc(42);
                color:$dark-grey;
                margin-bottom: rem-calc(30);
                sup{
                    color:$dark-grey;
                    top:-.45rem;
                    left:.15rem;
                }
                @media #{$breakpoint1}{
                    font-size:rem-calc(30);
                    line-height:rem-calc(34);
                    margin-bottom: rem-calc(20);
                }
            }
            p{
                font-size:rem-calc(18);
                line-height: rem-calc(23);
                color:$dark-grey;
                font-weight: normal;
                margin-top: 0;
                &:last-child{
                    margin-bottom: 0;
                }
                @media #{$breakpoint1}{
                    font-size:rem-calc(16);
                    line-height: rem-calc(21);
                }
            }

            .reveal-content{
                height:calc(100% - 65px);
                display: flex;
                justify-content: flex-start;
                flex-direction: column;
                padding:rem-calc(50);
                width: 100%;
                overflow: auto;
                .reveal-inner-content{
                    margin: auto;
                    @media #{$breakpoint1}{
                        margin:0;
                    }
                }
                @media #{$breakpoint1}{
                    justify-content: flex-start;
                    padding:$app-padding-sm;
                    height: auto;
                    order:2;
                    overflow: visible;
                }
            }

            .reveal-footer{
                height:rem-calc(65);
                width:100%;
                background-color:$intel-blue;
                display: flex;
                align-items: center;
                padding:0 rem-calc(50);
                z-index: 1;
                img{
                    width:rem-calc(55);
                    height:rem-calc(36);
                }
                @media #{$breakpoint1}{
                    height:rem-calc(50) !important;
                    padding:0 $app-padding-sm;
                    order:1;
                }
                span{
                    font-size:rem-calc(12);
                    line-height: rem-calc(16);
                    font-weight:bold;
                    color:$white;
                    letter-spacing:rem-calc(3);
                    text-transform: uppercase;
                    padding-left:rem-calc(25);
                    @media #{$breakpoint1}{
                        padding-left:rem-calc(12.5);
                    }
                }
            }
        }
    }

    .reveal-inner-container{
        width:100%;
        max-width: 100%;
        height: 100%;
        @media #{$breakpoint1}{
            height: auto;
        }
    }

    &.white-reveal{
        background-color:$white;
        .reveal-inner-container{
            @media #{$breakpoint1}{
                height: auto;
            }
        }
        .images-container{
            @media #{$breakpoint1}{
                height:rem-calc(175);
            }
            @media #{$breakpoint2}{
                background-position: center top;
            }
        }
        .content-container{
            background-color:$white;
            height: 100%;
            @media #{$breakpoint1}{
                height:auto;
            }
            .reveal-content{
                padding-top:0;
                padding-bottom:0;
                display: flex;
                flex-direction: column;
                justify-content: center;
                @media #{$breakpoint1}{
                    padding:rem-calc(25) $app-padding-sm;
                }
            }
            .reveal-title{
                padding-top:rem-calc(42);
                height:25%;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                @media #{$breakpoint1}{
                    padding-top:0;
                    height: auto;
                }
            }
            h1{
                color: $dark-grey;
                display: block;
                margin-bottom: 0;
            }
            p{
                font-size:rem-calc(16);
                line-height: rem-calc(21);
            }
        }
    }
    &.usage-model-reveal{
        .content-container{
            .reveal-title{
                @media #{$breakpoint1}{
                    padding-left:$app-padding-sm;
                }
            }
            .reveal-content{
                @media #{$breakpoint1}{
                    padding:$app-padding-sm $app-padding-sm $app-padding-sm 0;
                }
            }
            .accordion{
                @media #{$breakpoint1}{
                    padding-left:rem-calc(7.5);
                }
            }
        }
    }
    .accordion{
        padding:0;
        margin:rem-calc(15) 0 0 0;
        height:75%;
        @media #{$breakpoint1}{
            height:auto;
        }
        .accordion-item{
            margin-bottom: rem-calc(5);
            @media #{$breakpoint1}{
                margin-bottom:rem-calc(10);
            }
        }
        .accordion-title{
            border:0;
            padding:0;
            text-decoration: none;
            font-size:rem-calc(16);
            line-height: rem-calc(21);
            text-transform: uppercase;
            @extend %bold;
            color:$intel-blue;
            background-color:transparent;
            display: flex;
            align-items: center;
            padding:0;
            &:hover{
                background-color:transparent;
            }
            span{
                text-decoration: none;
                font-size:rem-calc(16);
                line-height: rem-calc(21);
                text-transform: uppercase;
                @extend %bold;
                color:$intel-blue;
                padding-left:rem-calc(5);
                padding-right:rem-calc(15);
                max-width:75%;
                @media #{$breakpoint1}{
                    font-size:rem-calc(14.5);
                    padding-right:rem-calc(20);
                }
            }
            img{
                position: relative;
                width:rem-calc(55);
                height:rem-calc(55);
                @media #{$breakpoint1}{
                    width:rem-calc(45);
                    height:rem-calc(45);
                }
            }
            &:before{
                font-size:rem-calc(24);
                line-height: 1;
                margin-top: rem-calc(-12);
            }
        }
        .accordion-content{
            border:0;
            padding:0;
            width:100%;
            padding-left:rem-calc(60);
            padding-right:rem-calc(60);
            margin-bottom:rem-calc(5);
            @media #{$breakpoint1}{
                padding-left: rem-calc(51);
                padding-right: rem-calc(15);
                margin-bottom:rem-calc(10);
            }
            p{
                color: $dark-grey;
                margin-bottom: 0;
            }
            div.caption-icon{
                display: flex;
                align-items: center;
                @media #{$breakpoint1}{
                    margin-top: rem-calc(7);
                }
                span{
                    position: relative;
                    margin-top: 0;
                    top: -1px;
                    font-weight: normal;
                    font-size:rem-calc(14.5);
                    line-height: rem-calc(18);
                    color:$dark-grey;
                }
                img{
                    position: relative;
                    width:rem-calc(55);
                    height:rem-calc(55);
                    @media #{$breakpoint1}{
                        width:rem-calc(45);
                        height:rem-calc(45);
                    }
                }
            }
        }
    }
}

