#sj_pre_recommendation{
    background-color: $white;
    nav{
        @media #{$breakpoint1} {
            background-color:$white;
            border-left:1px solid #979797;
            border-right:1px solid #979797;
            .legal-button{
                background-color: transparent;
                background-image: url("../images/icons/icon-legal.svg");
            }
            .close-button{
                background-image: url("../images/icons/icon-close.svg");
            }
            &:before{
                display: none;
            }
        }
    }

    header.recommendation-header{
        height:auto;
        @media #{$breakpoint1} {
            height: auto;
        }
        &:before{
            display: none;
        }
        .slide-instructions{
            height: 100%;
            position: relative;
            z-index: 1;
            padding: rem-calc(42) rem-calc(50) rem-calc(20);
            background:transparent;
            display:flex;
            justify-content: center;
            flex-direction: column;
            @media #{$breakpoint1} {
                height:auto;
                display: flex;
                z-index: 10;
                box-shadow:0 3px 3px 0 rgba(0,0,0,.15);
                padding:rem-calc(42) $app-padding-sm rem-calc(20);
            }
            div{
                display: flex;
                flex-direction: column;
                &:first-child{
                    justify-content: center;
                    height:65%;
                }
                &:last-child{
                    justify-content: flex-end;
                    height:35%;
                }
            }

            h1{
                width:100%;
                color:$dark-grey;
                margin: inherit;
                @media #{$breakpoint1} {
                   width: 100%;
                   text-align: center;
                   margin:0 auto;
                }
            }

            .downsell-messaging h3{
                @extend %regular;
                font-size:rem-calc(18);
                line-height:1.25;
                color:$dark-grey;
                margin-top:$app-padding-sm;
                width:100%;
                @media #{$breakpoint1} {
                    max-width:rem-calc(400);
                    text-align: center;
                    margin-left:auto;
                    margin-right: auto;
                    margin-top:rem-calc(5);
                    margin-bottom: 0;
                }
            }

            div:last-child{
                height: auto;
                justify-content: center;
            }

            @media #{$breakpoint1} {
                display: flex;
            }
        }
    }

    .configurator-template-container{
        margin:0;
        padding:0;
        .recommendations{
            @media #{$breakpoint1}{
                display:block;
                margin-top: 0;
            }
        }
    }

    .product-container{
        max-width: none;
        padding-left:rem-calc(30);
        padding-right:rem-calc(30);
        @media #{$breakpoint1}{
            padding-top:$app-padding-sm;
            padding-left:0;
            padding-right:0;
        }
    }

    .product-inner-container{
        opacity: 0;
        -webkit-transition: opacity 0.25s ease-in;
        -moz-transition: opacity 0.25s ease-in;
        transition: opacity 0.25s ease-in;
        .column-block{
            padding-left:rem-calc(20);
            padding-right:rem-calc(20);
            z-index: 2;

            -ms-flex: 1 1 0px;                  /*  IE11 fix  */
            -webkit-flex: 1 1 0;                /*  IE11 fix  */
            flex: 1;                            /*  IE11 fix  */

            @media #{$breakpoint1}{
                padding-left:0;
                padding-right:0;
            }
            img{
                display: block;
            }
        }
        .slick-slide{
            padding-left:$app-padding-sm;
            padding-right:$app-padding-sm;
        }
    }

    .pre-sidebar{
        background-color: $white;
        padding: rem-calc(100) rem-calc(42) rem-calc(42) rem-calc(25);

        -ms-flex: 1 1 0px;                  /*  IE11 fix  */
        -webkit-flex: 1 1 0;                /*  IE11 fix  */
        flex: 1;                            /*  IE11 fix  */

        @media only screen and (min-width:969px) and (max-width:1024px){
            -ms-flex: 0 0 30%;
            flex: 0 0 30%;
            max-width: 30%;
        }
        @media #{$breakpoint1}{
            text-align: center;
            padding:0 $app-padding-sm $app-padding-sm $app-padding-sm;
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%;
        }
        p{
            @extend %regular;
            font-size: rem-calc(14);
            line-height: rem-calc(19);
        }
        .button{
            width:100%;
            margin-top:rem-calc(20);
            @media #{$breakpoint1}{
                margin-top:0;
            }
        }
    }

} // end #sj_pre_recommendation

