/* ************ START MEDIA QUERIES ************* */
/* ********************************************** */
/* Internet Explorer 11 */
body.internet-explorer {
    .reveal.full .reveal-container {
        height: calc(100% - 55px);
    }

    #sj_device_configuration,#sj_device_configuration_Notebook,#sj_device_configuration_Chromebook,#sj_device_configuration_Desktop{
        .reveal-container {
            height: calc(100% - 55px);
            .content-container .reveal-title{
                height:auto;
            }
        }
        table tbody td:first-child {
            width: 38%;
        }
        .tabs li a {
            height: rem-calc(50);
        }
        .reveal-title{
            height: auto;
        }
    }

    .slick-initialized .slick-slide {
        visibility: hidden;

        .button-pre_recommendation {
            visibility: hidden;
        }
    }

    .slick-initialized .slick-slide {
        overflow-x: inherit;
        overflow-y: inherit;

        &.slick-current {
            display: block;
            visibility: visible;

            .button-pre_recommendation {
                visibility: visible;
            }
        }
    }
}
/* iOS 10 */
body.ios10 {
    /* iOS 10 - iPhone 5 Fix for Side Journey */
    .reveal.full.usage-model-reveal .content-container .reveal-content {
        @media only screen and (max-width:320px) {
            justify-content: flex-start;
            margin-top: 75px;
        }
        /* Portrait */
        @media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
            justify-content: flex-start;
            margin-top: 75px;
        }
        /* Landscape */
        @media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
            justify-content: flex-start;
            margin-top: 75px;
        }
    }
}
/* Apple iPad */
body.apple-ipad {
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);

    header {
        backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
    }

    .slick-slide {
        backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
    }

    .slide .slide-inner-content .thumbnail {
        -webkit-transition: none !important;
        -moz-transition: none !important;
        -o-transition: none !important;
        transition: none !important;

        img,
        p {
            transform: none !important;
            -webkit-transform: none !important;
            -ms-transform: none !important;
            -o-transform: none !important;
            -moz-transform: none !important;
        }

        &.selected {
            box-shadow: 4px 4px 8px 2px rgba(0,0,0,.15);
        }

        &:hover {
            transform: none !important;
            -webkit-transform: none !important;
            -ms-transform: none !important;
            -o-transform: none !important;
            -moz-transform: none !important;
        }
    }
}
