h1{
    @extend %light;
    color:$black;
    font-size:rem-calc(48);
    line-height:rem-calc(54);
    letter-spacing: rem-calc(-1.75);
    margin-top: 0;
    margin-bottom: 0;
    @media #{$breakpoint1}{
        font-size:rem-calc(30);
        line-height:rem-calc(38);
    }
}
h2 {
    @extend %light;
    color:$black;
    font-size:rem-calc(40);
    line-height:rem-calc(48);
    margin-top: 0;
    margin-bottom: 0;
    @media #{$breakpoint1}{
        font-size:rem-calc(20);
        line-height:rem-calc(22);
    }
}
h3 {
    @extend %bold;
    color:$black;
    font-size:rem-calc(30);
    line-height:rem-calc(34);
    margin-top: 0;
    margin-bottom: 0;
    @media #{$breakpoint1}{
        font-size:rem-calc(22);
        line-height:rem-calc(19);
    }
}
h4 {
    @extend %light;
    font-size: rem-calc(19);
    line-height: rem-calc(27);
    margin-top: 0;
    margin-bottom: rem-calc(25);
    @media #{$breakpoint1}{
        margin-top:rem-calc(5);
        margin-bottom:rem-calc(10);
        font-size:rem-calc(16);
        line-height: rem-calc(20);
    }
}
h5{
    @extend %regular;
    color: $med-grey;
    font-size: rem-calc(18);
    line-height: rem-calc(26);
    margin-top: 0;
    margin-bottom: rem-calc(25);
    @media #{$breakpoint1}{
        margin-top:rem-calc(10);
        margin-bottom:rem-calc(10);
        font-size:rem-calc(15);
        line-height: rem-calc(19);
    }
}
h6{
    @extend %bold;
    color:$black;
    font-size: rem-calc(16);
    line-height: rem-calc(17);
    margin-top: 0;
    margin-bottom: rem-calc(25);
}

p{
    @extend %regular;
    color:$black;
    font-size: rem-calc(16);
    line-height: rem-calc(24);
    margin-top: 0;
    @media #{$breakpoint1}{
        font-size: rem-calc(14);
        line-height: rem-calc(22);
    }

}

span{
    @extend %regular;
    color:$black;
    font-size: rem-calc(14);
    line-height: rem-calc(22);
    display: block;
}

sup{
    top:-.20rem;
    left:.025rem;
}

.button{
    background-color:$intel-yellow;
    @extend %regular;
    color: $intel-dark-blue;
    font-size: rem-calc(14);
    line-height: 1;
    text-align: center;
    display: block;
    padding:rem-calc(12) rem-calc(16);
    width:rem-calc(245);
    @include border-radius(3px);
    opacity: 1;
    transition: all 300ms ease;
    box-shadow: 4px 4px 8px 0px rgba(0,0,0,.25);
    &.blue-button{
        background-color:$intel-sky-blue;
        color:$white;
        &:hover{
            background-color: $intel-sky-blue-hover;
        }
        &:active{
            background-color: $intel-sky-blue-hover;
        }
    }
    &.button-complete{
        color:$intel-sky-blue;
        background-color:$white;
        pointer-events: none;
        box-shadow: none;
        line-height:rem-calc(18);
        opacity: 0;
        visibility: hidden;
    }
    &.copy-address-button{
        width: rem-calc(285);
    }
    &:disabled{
        background-color:#D8D8D8;
        color:#BBB;
        &:hover{
            cursor: default;
            background-color:#D8D8D8;
        }
    }
    &:hover{
        cursor: pointer;
        background-color:$intel-yellow-hover;
        text-decoration: none;
    }
    &:active{
        background-color:$intel-yellow-hover;
    }
}
