* {
    box-sizing: border-box;
}

html {
    border: 1px solid #979797;

    &.loading {
        height: 602px;
        overflow: hidden;

        .app-wrapper {
            visibility: hidden;
        }
    }
}

body,
html {
    width: 100%;
    max-width: $app-width;
    margin: 0 auto;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    background-color: transparent;
    box-sizing: border-box;
}

// this is a fix for the iframe embed issue
body {
    width: 1px;
    min-width: 100%;
}

#top {
    display: block;
    width: 100%;
    height: 0;
    background: transparent;
    position: absolute;
}

.preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
    background-color: $white;
    z-index: 9999;
    width: 100%;
    max-width: $app-width;
    height: 100%;
    max-height: 602px;
    border: 1px solid #979797;

    .loading-ring {
        display: inline-block;
        position: absolute;
        width: 64px;
        height: 64px;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
    }

    .loading-ring div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: 51px;
        height: 51px;
        margin: 6px;
        border: 6px solid $intel-blue;
        border-radius: 50%;
        animation: loading-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: $intel-blue transparent transparent transparent;
    }

    .loading-ring div:nth-child(1) {
        animation-delay: -0.45s;
    }

    .loading-ring div:nth-child(2) {
        animation-delay: -0.3s;
    }

    .loading-ring div:nth-child(3) {
        animation-delay: -0.15s;
    }

    @keyframes loading-ring {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    @media #{$breakpoint1} {
        max-height: none;
        height: 602px;
    }

}

::-webkit-scrollbar {
    display: none;
}

:focus {
    outline: none;
}

.app-wrapper {
    width: 100%;
    max-width: $app-width;
    margin: 0 auto;
    background-color: $white;
}

#app-render-template {
    width: 100%;
    max-width: $app-width;
    height: $app-height;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 0 auto;
    position: relative;
}

.desktop-layout {
    display: block;
    @media #{$breakpoint1} {
        display: none;
    }
}

.mobile-layout {
    display: none;
    @media #{$breakpoint1} {
        display: block;
    }
}

/* Start: Navigation Styles */
nav {
    width: 100%;
    height: rem-calc(42);
    background: transparent;
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    .button {
        background-position: center center;
        background-repeat: no-repeat;
        margin: 0;
        padding: 0;
        border: 0;
        border-radius: 0;
        height: rem-calc(42);
        width: rem-calc(42);
        min-width: 0;
        position: absolute;
        top: 0;

        @media #{$breakpoint1} {
            box-shadow: none;
        }

        &.back-button {
            z-index: 1;
            background-color: $intel-sky-blue;
            background-image: url("../images/icons/icon-back-alt.svg");
            background-repeat: no-repeat;
            @include background-size(26px 27px);
            left: 0;
            @media #{$breakpoint1} {
                background-color: transparent;
                background-image: url("../images/icons/icon-back.svg");
            }
        }

        &.legal-button {
            z-index: 1;
            background-color: #BBBBBB;
            background-image: url("../images/icons/icon-legal-alt.svg");
            background-repeat: no-repeat;
            @include background-size(21px 26px);
            right: 0;
            @media #{$breakpoint1} {
                background-color: transparent;
                background-image: url("../images/icons/icon-legal.svg");
            }
        }

        &.restart {
            background-image: url("../images/icons/icon-restart-alt.svg");
            background-repeat: no-repeat;
            @include background-size(28px 28px);
            @media #{$breakpoint1}{
                background-image: url("../images/icons/icon-restart-blue.svg");
            }
        }

        &.close-button {
            z-index: 1;
            background-color: $intel-sky-blue;
            background-image: url("../images/icons/icon-close-alt.svg");
            background-repeat: no-repeat;
            @include background-size(26px 26px);
            left: 0;
            @media #{$breakpoint1} {
                background-color: transparent;
            }
        }
    }
    .retailer-logo{
        display:none;
        z-index: 99999;
        max-width:rem-calc(300);
        width:rem-calc(150);
        height:rem-calc(42);
        position: absolute;
        left:rem-calc(42);
        margin-left:auto;
        margin-right:auto;
        background:transparent;
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
        box-shadow: 4px 4px 8px 0px rgba(0,0,0,.25);
        @media #{$breakpoint1} {
            left:0;
            right: 0;
        }
    }
}
/* End: Navigation Styles */

/* Start: Footer Styles */
footer {
    width: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: transparent;
    box-shadow: none;
    padding: 0 rem-calc(50) rem-calc(50);

    .button {
        margin: auto 0 0 auto;
        @media #{$breakpoint1} {
            margin: auto;
            width: 100%;
        }
    }

    .button-pre_recommendation {
        margin-top:rem-calc(15);
    }

    @media #{$breakpoint1} {
        background-color: $white;
        height: rem-calc(75);
        padding-left: $app-padding-sm;
        padding-right: $app-padding-sm;
        margin: 0 auto;
        padding-bottom: $app-padding-sm;
        position: relative;
        height: auto;
    }
}
/* End: Footer Styles */

/* Start: Slide Styles */
.slide {
    height: $app-height;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    @media #{$breakpoint1} {
        height: auto;
    }
    /* Start: Header */
    header {
        height: 50%;
        width: 100%;
        background-size: cover;
        background-position: center center;
        display: flex;
        flex-direction: column;
        position: relative;
        @media #{$breakpoint1} {
            height: rem-calc(200);
            background-position: top center;
            display: none;
        }
        /* BG Gradient for Text Readability */
        &:before {
            z-index: 0;
            content: '';
            display: block;
            position: absolute;
            left: 0;
            width: 80%;
            height: 100%;
            background: linear-gradient(to right,rgba(#000, 0.40),rgba(#000, 0));
            @media #{$breakpoint1} {
                bottom: 0;
                width: 100%;
                background: linear-gradient(to top,rgba(#000, 1),rgba(#000, 0));
            }
        }

        .slide-instructions {
            position: relative;
            z-index: 1;
            height: calc(100% - 72px); // 100 percent minus the negative -px amount that is overlapping
            padding: rem-calc(42) rem-calc(50) rem-calc(20);
            background: transparent;
            display: flex;
            justify-content: center;
            flex-direction: column;
            @media #{$breakpoint1} {
                height: auto;
                display: none;
                padding:rem-calc(42) $app-padding-sm rem-calc(20);
            }

            div {
                display: flex;
                flex-direction: column;

                &:first-child {
                    justify-content: flex-start;
                    height: 65%;
                }

                &:last-child {
                    justify-content: flex-end;
                    height: 35%;
                }
            }

            h1 {
                color: $white;
                margin: inherit;
                width: 45%;
                @media #{$breakpoint1} {
                    width: 100%;
                }
            }

            h2 {
                color: $white;
                font-size: rem-calc(22);
                line-height: rem-calc(25);
                @media #{$breakpoint1} {
                    text-align: center;
                    font-size: rem-calc(16);
                    line-height: rem-calc(19);
                }
            }

            h3 {
                color: $white;
                margin-top: inherit;
                margin-bottom: rem-calc(15);
                width: 50%;
                @media #{$breakpoint1} {
                    width: 100%;
                    text-align: center;
                }
            }

            h6 {
                color: $white;
                margin: inherit;
                padding-top: rem-calc(10);
                width: 50%;
                @media #{$breakpoint1} {
                    text-align: center;
                    width: 100%;
                    font-size: rem-calc(15);
                }
            }

            .button {
                align-self: flex-start;
                background: transparent;
                border: 1px solid $white;
                width: auto;
                min-width: rem-calc(160);
                max-width: 50%;
                margin: inherit;
                padding: rem-calc(2) rem-calc(8) rem-calc(10);
                font-size: rem-calc(11);
                line-height: normal;
                letter-spacing: rem-calc(1.7);
                @extend %bold;
                color: $white;
                text-align: center;
                box-shadow: none;

                &:after {
                    content: url("../images/icons/icon-learn-more-alt.svg");
                    position: relative;
                    top: rem-calc(5.28);
                    margin-left: rem-calc(8);
                    width: rem-calc(18);
                    display: inline-block;
                }

                &:hover {
                    text-decoration: underline;
                    transition: all 200ms ease;
                }

                @media #{$breakpoint1} {
                    align-self: center;
                }

            }
        }

        &.usage-model-category-header {
            @media #{$breakpoint1} {
                height: rem-calc(280);
                .slide-instructions {
                    display: flex;
                    height: 100%;
                    padding: $app-padding-sm $app-padding-sm rem-calc(20);
                    div:first-child {
                        justify-content: flex-end;
                    }
                }
            }
        }

        &.recommendation-header {
            height:auto;

            @media #{$breakpoint1} {
                display: block;
            }

            &:before {
                display: none;
            }

            .slide-instructions {
                height: 100%;
                padding-bottom:0;

                div:last-child {
                    height: auto;
                    justify-content: center;
                }

                .downsell-messaging{
                    h3{
                        margin-bottom:0;
                    }
                }

                h1 {
                    width: 100%;
                    color:$dark-grey;
                    @media #{$breakpoint1} {
                        width: 100%;
                        text-align: center;
                    }
                }

                .downsell-messaging h3{
                    @extend %regular;
                    font-size:rem-calc(18);
                    line-height:1.25;
                    color:$dark-grey;
                    margin-top:$app-padding-sm / 2;
                    width:100%;
                    @media #{$breakpoint1} {
                        max-width:rem-calc(400);
                        text-align: center;
                        margin-left:auto;
                        margin-right: auto;
                        margin-top:rem-calc(5);
                        margin-bottom: 0;
                    }
                }

                @media #{$breakpoint1} {
                    display: flex;
                    z-index: 10;
                    padding-bottom: 1.25rem;
                    box-shadow:0 3px 3px 0 rgba(0,0,0,.15);
                }
            }
        }
    }
    /* End: Header */
    .slide-instructions-mobile {
        display: none;
        text-align: center;
        padding: rem-calc(42) $app-padding-sm rem-calc(23) $app-padding-sm;

        h1 {
            margin: inherit;
            font-size:rem-calc(30);
            line-height: 1;
            color:$dark-grey;
            letter-spacing: rem-calc(.25);
        }

        h2 {
            text-align: center;
            font-size: rem-calc(16);
            line-height: rem-calc(19);
        }

        h3 {
            margin-top: inherit;
            margin-bottom: rem-calc(15);
            width: 100%;
            text-align: center;
        }

        h4 {
            margin-bottom: 0;
        }

        h6 {
            margin: inherit;
            padding-top: rem-calc(15);
            text-align: center;
            width: 100%;
            font-size: rem-calc(16);
            line-height: rem-calc(17);
            color:$dark-grey;
            @extend %regular;
        }

        .button {
            align-self: flex-start;
            background: transparent;
            border: 1px solid $dark-grey;
            width: auto;
            min-width: rem-calc(160);
            max-width: 50%;
            margin: inherit;
            padding: rem-calc(2) rem-calc(8) rem-calc(10);
            font-size: rem-calc(11);
            line-height: normal;
            letter-spacing: rem-calc(1.7);
            @extend %bold;
            color: $dark-grey;
            text-align: center;
            box-shadow: none;

            &:after {
                content: url("../images/icons/icon-learn-more-alt.svg");
                position: relative;
                top: rem-calc(5.28);
                margin-left: rem-calc(8);
                width: rem-calc(18);
                display: inline-block;
                @media #{$breakpoint1} {
                    content: url("../images/icons/icon-learn-more.svg");
                }
            }

            &:hover {
                text-decoration: underline;
                transition: all 200ms ease;
            }

            @media #{$breakpoint1} {
                align-self: center;
                margin:0 auto;
            }
        }
        @media #{$breakpoint1} {
            display: block;
        }
    }

    .slide-container {
        height: 100%;
        @media #{$breakpoint1} {
            height: auto;
        }
    }

    .slide-content {
        height: 50%;
        position: relative;
        top: -72px;
        @media #{$breakpoint1} {
            top: 0;
            height: auto;
        }
        .row {
            max-width: none;
            padding-left: rem-calc(30);
            padding-right: rem-calc(30);
            @media #{$breakpoint1} {
                padding-left: $app-padding-sm;
                padding-right: $app-padding-sm;
            }
            .column-block {
                padding-left: rem-calc(20);
                padding-right: rem-calc(20);
                z-index: 2;
                @media #{$breakpoint1} {
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }
    }

    .configurator-template-row .slide-content {
        top: 0;
        margin:0;
        height: auto;
    }

    .slide-inner-content {
        width: 100%;
        max-width: 100%;
        margin:0 auto;

        @media #{$breakpoint1} {
            padding-bottom: $app-padding-sm;
        }

        .button {
            background: transparent;
            width: 100%;
            max-width: 100%;
            min-width: 0;
            margin-top: rem-calc(10);
            padding: rem-calc(5) 0;
            font-size: rem-calc(11);
            line-height: normal;
            letter-spacing: rem-calc(1.7);
            @extend %bold;
            color: $grey;
            text-align: right;
            box-shadow: none;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            &:after {
                content: url("../images/icons/icon-learn-more.svg");
                position: relative;
                margin-left: rem-calc(8);
                width: rem-calc(18);
                height: rem-calc(18);
                display: block;
            }

            &:hover {
                text-decoration: none;
                color:$dark-grey;
                transition: all 200ms ease;
            }

            @media #{$breakpoint1} {
                margin-top: 0;
                width: 25%;
                text-align: right;
                justify-content: center;
            }

            @media #{$breakpoint3} {
                padding-left: rem-calc(8);
            }

        }

        .column-block {
            margin-bottom: rem-calc(9.68);
            // Go full width at breakpoint
            @media #{$breakpoint1} {
                display: flex;
                width: 100%;
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: rem-calc(10);
            }
        }

        .thumbnail {
            background: $white;
            box-shadow: 4px 4px 8px 2px rgba(0,0,0,.15);
            position: relative;
            width: 100%;
            @include border-radius(2px);
            align-items: center;
            justify-content: flex-start;
            display: flex;
            flex-direction: column;
            transition: all 250ms ease;
            border: 1px solid #BBBBBB;
            border: 1px solid $border-color;
            padding: rem-calc(2) rem-calc(12) rem-calc(12); // padding cannot change without recaculating .thumbnail.selected padding + border
            font-smoothing: antialiased;
            -webkit-font-smoothing: antialiased;
            backface-visibility: hidden;
            -webkit-backface-visibility: hidden;
            min-height: rem-calc(130);
            @media #{$breakpoint1} {
                padding: rem-calc(4) rem-calc(10);
                justify-content: flex-start;
                flex-direction: row;
                width: 75%;
                height: auto;
                min-height: rem-calc(0);
            }
            @media #{$breakpoint3} {
                padding: rem-calc(4) rem-calc(8);
            }

            img {
                display: block;
                position: relative;
                backface-visibility: hidden;
                -webkit-backface-visibility: hidden;
                -ms-transform: translateZ(0);
                /* IE 9 */
                -webkit-transform: translateZ(0);
                /* Chrome, Safari, Opera */
                transform: translateZ(0);
                width: 80px;
                height: 80px;
                flex-shrink: 0;
                @media #{$breakpoint1} {
                    width: rem-calc(45);
                    height: rem-calc(45);
                    margin-right: rem-calc(10);
                }
            }

            div {
                display: flex;
                align-items: center;
                width: 100%;
            }

            p {
                display: block;
                margin: 0 auto;
                padding: 0;
                font-size: rem-calc(18);
                line-height: rem-calc(20);
                width: 100%;
                @extend %light;
                color: $dark-grey;
                text-align: center;
                font-smoothing: antialiased;
                -webkit-font-smoothing: antialiased;
                backface-visibility: hidden;
                -webkit-backface-visibility: hidden;
                -ms-transform: translateZ(0);
                /* IE 9 */
                -webkit-transform: translateZ(0);
                /* Chrome, Safari, Opera */
                transform: translateZ(0);
                max-width: rem-calc(180);
                @media #{$breakpoint1} {
                    font-size: rem-calc(18);
                    line-height: 1;
                    text-align: left;
                    font-weight: normal;
                    max-width: 100%;
                    margin: 0;
                }
                @media #{$breakpoint3} {
                    font-size: rem-calc(16);
                    line-height: 1;
                }
                @media only screen and (max-width:320px) {
                    font-size: rem-calc(14);
                }
            }

            &:hover {
                cursor: pointer;
                backface-visibility: hidden;
                -webkit-backface-visibility: hidden;
                transform: scale(1.1,1.1);
                -webkit-transform: scale(1.1,1.1);
                -ms-transform: scale(1.1,1.1);
                -o-transform: scale(1.1,1.1);
                -moz-transform: scale(1.1,1.1);
                @media #{$breakpoint1} {
                    box-shadow: 4px 4px 8px 2px rgba(0,0,0,.15);
                    transition: none;
                    transform: none;
                    -webkit-transform: none;
                    -ms-transform: none;
                    -moz-transform: none;
                    -o-transform: none;
                }
            }
            // touch hover?
            // @media (hover: none) {
            //     &:hover {
            //         transform: none;
            //         -webkit-transform: none;
            //         -ms-transform: none;
            //         -moz-transform: none;
            //         -o-transform: none;
            //     }
            // }
        }

        .thumbnail.selected {
            box-shadow: none;
            padding: rem-calc(0) rem-calc(10) rem-calc(18);
            padding: rem-calc(0) rem-calc(10) rem-calc(10);
            border:3px solid $intel-yellow;

            &:hover {
                transform: scale(1.0,1.0);
                -webkit-transform: scale(1.0,1.0);
                -ms-transform: scale(1.0,1.0);
                -o-transform: scale(1.0,1.0);
                -moz-transform: scale(1.0,1.0);
            }

            @media #{$breakpoint1} {
                padding: rem-calc(2) rem-calc(8);
                border: 3px solid $intel-yellow;

                &:hover {
                    transition: none;
                    transform: none;
                    -webkit-transform: none;
                    -ms-transform: none;
                    -moz-transform: none;
                    -o-transform: none;
                }
            }

            @media #{$breakpoint3} {
                padding: rem-calc(2) rem-calc(6);
            }

        }
    }

    /* Start Slide */
    &.slide-start {

        header {
            background-image: url("../images/bg-start.jpg");
            &:before{
                width:65%;
            }
            .slide-instructions{
                height:100%;
                padding:2.625rem 3.125rem 2.625rem;
                h1{
                    width:40%;
                    margin-top:rem-calc(10);
                }
                div:last-child{
                    height:100%;
                    justify-content: center;
                }
            }
        }

        .slide-content {
            top: 0;

            h1,
            p {
                text-align: left;
                color: $dark-grey;
                @media #{$breakpoint1} {
                    text-align: center;
                    max-width: none;
                }
            }

            h1 {
                @media #{$breakpoint1} {
                    font-size: rem-calc(36);
                    line-height: 1;
                    max-width:rem-calc(300);
                    margin:0 auto;
                }
            }

            p {
                font-size: rem-calc(18);
                line-height: rem-calc(26);
                margin-top: 0;
                margin-bottom: rem-calc(25);
                @media #{$breakpoint1} {
                    font-size: rem-calc(15);
                    line-height: rem-calc(19);
                    margin-top: rem-calc(10);
                    margin-bottom: rem-calc(10);
                }
            }

            .start-button {
                opacity: 1 !important;
            }

            .row {
                width: calc(100% - 25%);
                margin-left: 0;
                padding-left:0;
                padding-right: 2.5rem;
                margin-left:-1.25rem;
                @media #{$breakpoint1} {
                    width: 100%;
                    margin-left: 0;
                    padding-right: 0;
                }
            }
        }

        .slide-inner-content {
            padding: rem-calc(50) rem-calc(50) 0;
            margin-left: 0;

            @media #{$breakpoint1} {
                width: 100%;
                padding-top: 0;
                padding-left: $app-padding-sm;
                padding-right: $app-padding-sm;
                &:last-child {
                    padding-bottom: $app-padding-sm;
                }
            }

            .column-block{
                text-align: center;
                @media #{$breakpoint1} {
                    display: flex;
                    text-align: left;
                    width: 100%;
                    div{
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        text-align: left;
                        width: 100%;
                        max-width: rem-calc(300);
                        margin:0 auto;
                        height:auto;
                        div{
                            display: block;
                            width: 100%;
                            max-width:rem-calc(150);
                            margin:0 0 0 rem-calc(10);
                        }
                    }
                }
                img{
                    margin:0 auto;
                    max-width:rem-calc(100);
                    @media #{$breakpoint1} {
                        margin:0;
                        max-width:rem-calc(80);
                        margin-left:rem-calc(-10);
                    }
                }
                p{
                    text-align: center;
                    max-width:rem-calc(225);
                    margin:0 auto;
                    @media #{$breakpoint1} {
                        width:100%;
                        max-width:rem-calc(300);
                        text-align: left;
                        margin-left: 0;
                        font-size:rem-calc(16);
                        line-height:rem-calc(19);
                    }
                }
                &:nth-child(2) p{
                    max-width:rem-calc(215);
                }
            }

        }

        .intel-logo{
            display: block;
            position: relative;
            width:rem-calc(75);
            height:rem-calc(39);
            left:rem-calc(-5);
            margin-top:rem-calc(-10);
            margin-bottom: rem-calc(20);
            @media #{$breakpoint1} {
                margin-left:auto;
                margin-right:auto;
                left:0;
            }
        }

        footer{
            .button-pre_recommendation{
                visibility: hidden;
                opacity: 0;
                @media #{$breakpoint1} {
                    display: none;
                }
            }
        }

    }
    /* Form Factor Slide */
    &.slide-form_factor {

        header {
            background-image: url("../images/bg-form-factor.jpg");
            background-position: top;
        }

        .column-block {
            @media #{$breakpoint1} {
                display: flex;
                flex-direction: row;
                width: 100%;
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: rem-calc(10);
            }
            .button {
                @media #{$breakpoint1} {
                    margin-top: 0;
                    width: 25%;
                    text-align: right;
                    justify-content: center;
                }

                @media #{$breakpoint3} {
                    padding-left: rem-calc(8);
                }
            }
        }

        .thumbnail {
            padding: rem-calc(12) rem-calc(12) rem-calc(20);

            @media #{$breakpoint1} {
                padding: rem-calc(4) rem-calc(10);
                justify-content: flex-start;
                flex-direction: row;
                width: 75%;
                height: auto;
                min-height: rem-calc(55);
            }

            @media #{$breakpoint3} {
                padding: rem-calc(4) rem-calc(8);
            }

            p {
                @extend %light;
                padding-top: rem-calc(15);
                padding-left: 14%;
                padding-right: 14%;
                max-width: rem-calc(295);
                margin: 0 auto;
                @media #{$breakpoint1} {
                    font-size: rem-calc(18);
                    line-height: 1;
                    text-align: left;
                    font-weight: normal;
                    max-width: 100%;
                    margin: 0;
                    padding-top: 0;
                    padding-left:0;
                    padding-right:0;
                }
                @media #{$breakpoint3} {
                    font-size: rem-calc(16);
                    line-height: 1;
                }
                @media only screen and (max-width:320px) {
                    font-size: rem-calc(14);
                }
            }

            img {
                width: 100%;
                max-width: rem-calc(295);
                height: auto;
                padding-left:10px;
                padding-right: 10px;
                &.icon-mobile{
                    display: none;
                }
                @media #{$breakpoint1} {
                    width: rem-calc(45);
                    height: rem-calc(45);
                    margin-right: rem-calc(10);
                    display: none;
                    &.icon-mobile{
                        display: block;
                    }
                }
            }
        }

        .thumbnail.selected {

            padding: rem-calc(10) rem-calc(10) rem-calc(18);

            @media #{$breakpoint1} {
                padding: rem-calc(2) rem-calc(8);
                border: 3px solid $intel-yellow;

                &:hover {
                    transition: none;
                    transform: none;
                    -webkit-transform: none;
                    -ms-transform: none;
                    -moz-transform: none;
                    -o-transform: none;
                }
            }

            @media #{$breakpoint3} {
                padding: rem-calc(2) rem-calc(6);
            }
        }


        footer{
            .button-pre_recommendation{
                visibility: hidden;
                opacity: 0;
            }
        }
    }
    /* Categories Slide */
    &.slide-categories {

        header {
            background-image: url("../images/bg-categories.jpg");
        }

        .slide-content .row {
            width: calc(100% - 25%);
            margin-left: 0;
            @media #{$breakpoint1} {
                width: 100%;
            }
        }

        @media #{$breakpoint1} {
            .slide-inner-content .thumbnail {
                height: auto !important;
            }
        }
    }
    /* Usage Model Categories Slides */
    &.slide-usage_model_category {
        header.usage-model-category-productivity-bg,header.usage-model-category-Chromebook_productivity-bg,header.usage-model-category-Desktop_productivity-bg,header.usage-model-category-Notebook_productivity-bg {
            background-image: url("../images/bg-productivity.jpg");
            @media #{$breakpoint3} {
                background-position: bottom;
            }
        }

        header.usage-model-category-entertainment-bg,header.usage-model-category-Chromebook_entertainment-bg,header.usage-model-category-Desktop_entertainment-bg,header.usage-model-category-Notebook_entertainment-bg {
            background-image: url("../images/bg-entertainment.jpg");
            background-position: top;
            @media #{$breakpoint3} {
                background-position: bottom;
            }
        }

        header.usage-model-category-photo-video-bg,header.usage-model-category-Chromebook_photo-video-bg,header.usage-model-category-Desktop_photo-video-bg,header.usage-model-category-Notebook_photo-video-bg{
            background-image: url("../images/bg-photo-video.jpg");
            @media #{$breakpoint3} {
                background-position: bottom;
            }
        }

        header.usage-model-category-education-bg,header.usage-model-category-Chromebook_education-bg,header.usage-model-category-Desktop_education-bg,header.usage-model-category-Notebook_education-bg{
            background-image: url("../images/bg-education.jpg");
            @media #{$breakpoint3} {
                background-position: bottom;
            }
        }

        header.usage-model-category-gaming-bg,header.usage-model-category-Chromebook_gaming-bg,header.usage-model-category-Desktop_gaming-bg,header.usage-model-category-Notebook_gaming-bg{
            background-image: url("../images/bg-gaming.jpg");
            @media #{$breakpoint3} {
                background-position: bottom;
            }
        }

        .slide-inner-content .thumbnail {
            padding: rem-calc(15) rem-calc(12) rem-calc(30);
            @media #{$breakpoint1} {
                padding: rem-calc(4) rem-calc(10);
            }
        }

        .slide-inner-content .thumbnail.selected {
            padding: rem-calc(13) rem-calc(10) rem-calc(28);
            @media #{$breakpoint1} {
                padding: rem-calc(2) rem-calc(8);
            }
        }

        @media #{$breakpoint1} {

            .slide-content {
                height: auto;
            }

            .slide-inner-content {
                .thumbnail {
                    width: 100%;
                    height: auto !important;
                }
            }

            .slide-instructions-mobile{
                h1{
                    margin-bottom: rem-calc(15);
                }
                h4{
                    font-size:rem-calc(18);
                    line-height: rem-calc(19);
                    padding-top: rem-calc(20);
                    margin-top:0;
                }
                h6{
                    padding-top:rem-calc(10);
                }
            }

        }
    }
}
/* End: Slide Styles */

/* Start: Table Styles */
table {
    width: 100%;
    table-layout: fixed;
    background: $white;
    border-left: 1px solid $border-color;
    border-right: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    thead {
        td {
            padding: rem-calc(10);
            border-top: 1px solid $white;
            border-right: 1px solid $white;
            font-size: rem-calc(14);
            &:last-child {
                border-right: 1px solid $white;
            }
        }
        img {
            display: block;
            margin: 0 auto;
        }
    }
    tbody {
        td {
            padding: rem-calc(10);
            border-top: 1px solid $border-color;
            border-right: 1px solid $border-color;
            font-size: rem-calc(14);
            &:last-child {
                border-right: 1px solid $white;
            }
            div {
                display: flex;
                align-items: center;
                width: 100%;
            }

            img {
                margin: 0 auto;
                display: block;
            }
        }
    }
}
/* End: Table Styles */
/* ***************************************** */

/* Start: Layout Styles */
.hidden {
    display: none;
    height: 0;
    width: 0;
    text-indent: -9999px;
}

.slick-slide {
    height: $app-height !important;
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    @media #{$breakpoint1} {
        height: 100vh !important;
        max-height: $app-height;
    }
}

.slick-active {
    height: $app-height !important;
    @media #{$breakpoint1} {
        height: 100% !important;
    }
}

/* Start: Recommendation Screen */
.configurator-template-row {
    max-width: 100%;
    height: 100%;
    @media #{$breakpoint1} {
        height: auto;
    }
    .configurator-template-content {
        padding-left: 0;
        padding-right: 0;
        @media only screen and (min-width:969px) and (max-width:1024px){
            -ms-flex: 0 0 70%;
            flex: 0 0 70%;
            max-width: 70%;
        }
        @media #{$breakpoint1} {
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%;
        }
    }

    .wim-messaging{
        padding:$app-padding-sm;
        text-align: center;
        @extend %bold;
        color:$dark-grey;
        font-size: rem-calc(18);
        line-height: rem-calc(26);
        width:100%;

        @media only screen and (max-width: 1024px) and (min-width: 969px){
            padding-top:$app-padding-sm;
            font-size: rem-calc(16);
            line-height: rem-calc(24);
        }

        @media #{$breakpoint1} {
            padding-top:0;
            font-size: rem-calc(18);
            line-height: rem-calc(26);
        }

    }

    &.downsell-messaging-active{

        .slide-instructions{
            padding-top:rem-calc(34);
        }

        @media only screen and (max-width:1199px){
            .wim-messaging{
                padding-top:0;
            }
        }

        @media only screen and (max-width:1199px){
            .configurator-template-container .column-block .column-content{
                padding-top:rem-calc(7.5);
            }
        }

        @media #{$breakpoint1} {
            .configurator-template-container .column-block .column-content{
                padding-top:0;
            }
        }

    }

}
.configurator-template-container {
    margin: 0;
    padding: 0;

    .recommendations {
        padding: 0;
        @media #{$breakpoint1} {
            display: none;
        }
    }

    .recommendations-mobile {
        display: none;
        @media #{$breakpoint1} {
            display: block;
        }
    }

    .product-container {
        margin: 0;

        @media #{$breakpoint1} {
            padding-left:0 !important;
            padding-right:0 !important;
        }

        .product-inner-container {
            display: inherit !important;
            width: 100%;

            @media #{$breakpoint1} {
                display: block !important;
                width: 100%;

                .slick-slide {
                    height: auto !important;
                    padding-left:$app-padding-sm;
                    padding-right:$app-padding-sm;
                }

                .slick-active {
                    height: auto !important;
                }

                .slick-dots {
                    .slick-active,
                    .slick-slide {
                        height: auto !important;
                    }
                }
            }
        }
    }

    .column-block {
        padding-left: rem-calc(10);
        padding-right: rem-calc(10);
        margin-bottom:0;
        display: block;
        max-width:rem-calc(300);

        -ms-flex: 1 1 0px;                  /*  IE11 fix  */
        -webkit-flex: 1 1 0;                /*  IE11 fix  */
        flex: 1;                            /*  IE11 fix  */

        @media only screen and (min-width:969px) and (max-width:1150px) {
            padding-right: 0;
        }

        @media #{$breakpoint1} {
            padding: 0;
            border-right: 0;
            margin-bottom: 0;
            width: auto;
            display: block;
            min-height: 0;
            min-width: 0;
            max-width:100%;
        }

        .column-content {
            background: $white;
            max-width: rem-calc(300);
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            padding: rem-calc(15) rem-calc(20) rem-calc(20);
            @include border-radius(2px);
            @media only screen and (min-width:969px) and (max-width:1024px) {
                padding: rem-calc(10) rem-calc(0) rem-calc(15);
            }
            @media #{$breakpoint1} {
                border: none;
                box-shadow: none;
                max-width:100%;
                height: 100%;
                display:block;
                padding-top:0;
                padding-left:0;
                padding-right:0;
            }
            .column-inner-content:last-child{
                margin-top: auto;
            }
        }

        img {
            margin-left: auto;
            margin-right: auto;
            margin-bottom: rem-calc(15);
            max-height: rem-calc(120);
            @media only screen and (max-width:1120px) {
                margin-bottom: rem-calc(10);
            }
            @media only screen and (max-width:1045px) {
                width: rem-calc(120);
            }
            @media only screen and (min-width:969px) {
                min-height: rem-calc(75);
            }
            @media only screen and (min-width:969px) and (max-width:1024px) {
                width: rem-calc(100);
            }
        }

        .product-title {
            font-size: rem-calc(16);
            @extend %bold;
            color: $black;
            text-align: left;
            display: block;
            margin-bottom: rem-calc(10);
            @media #{$breakpoint1} {
                text-align: center;
                max-width:rem-calc(280);
                margin:0 auto;
            }
        }

        .shop-price-label {
            position: relative;
            margin-top: rem-calc(15);
            text-align: left;
            @media #{$breakpoint1} {
                text-align: center;
            }
        }

        .product-chip,
        .product-detail {
            font-size: rem-calc(14);
            line-height: rem-calc(19);
            margin-bottom: rem-calc(4);
            @extend %regular;
            text-align: left;
            color: $dark-grey;
            @media #{$breakpoint1} {
                text-align: center;
            }
        }

        .product-price {
            font-size: rem-calc(18);
            @extend %bold;
            color: $black;
            text-align: left;
            @media #{$breakpoint1} {
                text-align: center;
            }
        }

        .button {
            width:100%;
            display: block;
            text-decoration: none;
            margin-top: rem-calc(20);
        }
    }
}

.sidebar {
    padding: rem-calc(42) rem-calc(42) rem-calc(42) rem-calc(25);
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content:center;

    -ms-flex: 1 1 0px;                  /*  IE11 fix  */
    -webkit-flex: 1 1 0;                /*  IE11 fix  */
    flex: 1;                            /*  IE11 fix  */

    @media only screen and (min-width:969px) and (max-width:1024px){
        -ms-flex: 0 0 30%;
        flex: 0 0 30%;
        max-width: 30%;
    }

    @media #{$breakpoint1} {
        padding: $app-padding-sm 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        max-height: none;
        overflow: visible;
        display: none;
    }

    .sidebar-container{
        border:0;
        padding:0;
        div:first-child{
            padding-top:0;
        }
    }

    h3 {
        color: $black;
        @extend %bold;
        font-size: rem-calc(14);
        line-height: rem-calc(17);
        text-align: left;
        margin-top: 0;
        margin-bottom: rem-calc(13);
        @media #{$breakpoint1} {
            padding-left:$app-padding-sm;
            padding-right:$app-padding-sm;
        }
    }

    .button,
    .powered-by-button,
    .restart-button{
        padding: 0;
        min-width: 0;
        width:100%;
        background: transparent;
        text-decoration: none;
        text-align: left;
        font-size: rem-calc(14);
        line-height: rem-calc(17);
        box-shadow: none;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border-bottom: none;

        span{
            display: block;
        }

        img{
            display: block;
            margin-left:auto;
            padding-left:rem-calc(50);
            max-width:62px;
            min-height:21px;
        }

        &:hover {
            background-color: transparent;
            text-decoration: none;
            color: $intel-sky-blue;
            cursor: pointer;
            span{
                color: $intel-sky-blue;
                transition: all 200ms ease;
            }
        }

        @media #{$breakpoint1} {
            font-size: rem-calc(11);
            line-height: rem-calc(14);
            padding-left: $app-padding-sm;
            padding-right: $app-padding-sm;
        }
    }

    ul {
        margin-left: 0;
        padding-left: 0;
    }

    li {
        list-style-type: none;
        font-size: rem-calc(13);
        line-height: rem-calc(16);
        margin-bottom: rem-calc(4);
        @extend %regular;
        text-align: left;
        color: $dark-grey;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
    }

    div {
        border-bottom: rem-calc(1) solid #BBBBBB;
        padding:rem-calc(24) 0;
        &:last-child{
            border-bottom: 0;
        }
        @media #{$breakpoint1}{
            padding:$app-padding-sm 0;
        }
    }

    .powered-by-chip-container {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: flex-start;
        border-bottom: 0;
        padding-bottom:rem-calc(2);
        @media #{$breakpoint1} {
            margin: 0 auto;
            padding-left: $app-padding-sm;
            padding-right: $app-padding-sm;
        }
    }

    .powered-by-text {
        // padding-left: rem-calc(20);
        padding-left: rem-calc(0);
        padding-right: rem-calc(20);
        font-size: rem-calc(14);
        line-height: rem-calc(17);
        margin-bottom: 0;
        color:$dark-grey;
        @media #{$breakpoint1} {
            margin: 0;
            padding-left: rem-calc(0);
            padding-right: $app-padding-sm;
            // max-width: rem-calc(155);
        }
    }

    .powered-by-chip {
        display: block;
        width: rem-calc(50);
        height: rem-calc(50);
        @include background-size(contain);
        background-repeat: no-repeat;
        background-position: center center;
        border-bottom: none;
        flex-shrink: 0;
        @media #{$breakpoint1} {
            width: rem-calc(40);
            height: rem-calc(40);
        }

        &[data-id="i9"] {
            background-image: url("../images/i9.png");
        }

        &[data-id="i7"] {
            background-image: url("../images/i7.png");
        }

        &[data-id="i5"] {
            background-image: url("../images/i5.png");
        }

        &[data-id="i3"] {
            background-image: url("../images/i3.png");
        }

        &[data-id="celeron"] {
            background-image: url("../images/celeron.png");
        }

        &[data-id="pentium"] {
            background-image: url("../images/pentium.png");
        }
    }
}

/* Start: Recommendation Tabs */
.recommendations-mobile {
    padding:0;
    .tabs {
        padding-left: 0;
        border: 0;
        display: flex;
        flex-direction: row;
        min-height:rem-calc(40);
        li {
            width: 50%;
            cursor: pointer;
            background-color: $border-color;
            display: flex;
            flex-direction: column;
            justify-content: center;
            a {
                background-color:transparent;
                border-left:1px solid #e1e1e1;
                border-right:1px solid #e1e1e1;
                color: $dark-grey;
