@font-face	{
	font-family: 'Intel Clear';
	src: url('../fonts/intelclear_rg-webfont.woff') format('woff'),
	     url('../fonts/intelclear_rg-webfont.ttf') format('truetype');
	font-weight: normal;
}
@font-face	{
	font-family: 'Intel Clear';
	src: url('../fonts/intelclear_bd-webfont.woff') format('woff'),
	     url('../fonts/intelclear_bd-webfont.ttf') format('truetype');
	font-weight: bold;
}
@font-face	{
	font-family: 'Intel Clear';
	src: url('../fonts/intelclear_lt-webfont.woff') format('woff'),
	     url('../fonts/intelclear_lt-webfont.ttf') format('truetype');
	font-weight: 100;
}
%regular{
	font-family: "Intel Clear", Helvetica, Arial, sans-serif;
	font-weight: normal;
	-webkit-font-smoothing: antialiased;
	text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}
%bold{
	font-family: "Intel Clear", Helvetica, Arial, sans-serif;
	font-weight: bold;
	-webkit-font-smoothing: antialiased;
	text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}
%light{
	font-family: "Intel Clear", Helvetica, Arial, sans-serif;
	font-weight: 100;
	-webkit-font-smoothing: antialiased;
	text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}
